
// object转Url
export function objectUrl(obj) {
  let url = '';
  let kvArr = Object.entries(obj);
  kvArr.forEach(v => {
    if (Object.prototype.toString.call(v[1]) == '[object Object]') {
      arguments.callee(v[1]);
    } else {
      url += v.join('=') + '&'
    }
  })
  return url.substring(0, url.length - 1);
}