<!--
 * @Description: 分销员招募
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <el-card style="padding-bottom:20px;"
             shadow="never">
      <div>
        <el-button type="primary"
                   @click="goToNew">新增模板</el-button>
      </div>
      <div style="margin-top:20px;">
        <el-table :data="list"
                  border
                  style="width: 100%"
                  v-loading="loading">
          <el-table-column label="宣传图">
            <template slot-scope="scope">
              <el-image :src="scope.row.recruitUrl"
                        style="width: 100px; height: 100px"
                        fit="fill"></el-image>
            </template>
          </el-table-column>
          <el-table-column prop="title"
                           label="招募标题"></el-table-column>
          <el-table-column label="推广链接">
            <template slot-scope="scope">
              <div class="disStr">
                <span>{{scope.row.herfUrl}}</span>
                <el-button plain
                           style="margin-left:4px;"
                           class="cobyOrderSn"
                           type="primary"
                           size="mini"
                           :data-clipboard-text="scope.row.herfUrl"
                           @click="copyLink">复制链接</el-button>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text"
                         size="small"
                         @click="getPic(scope.row)">下载宣传图</el-button>
              <el-button type="text"
                         size="small"
                         @click="getQrcode(scope.row)">下载二维码</el-button>
              <el-button type="text"
                         size="small"
                         @click="showDetails(scope.row)">预览</el-button>
              <el-button type="text"
                         size="small"
                         style="color:#909399;"
                         v-if="scope.row.isDefault == 1">默认</el-button>
              <el-button type="text"
                         size="small"
                         v-if="scope.row.isDefault != 1"
                         @click="setRecruit(scope.row)">设置默认</el-button>
              <el-button type="text"
                         size="small"
                         style="color:#909399;"
                         v-if="scope.row.isDefault != 1"
                         @click="deleteItem(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
    <!-- 新增弹出框 -->
    <el-dialog title="新增推广链接"
               :visible.sync="showNew"
               center>
      <div>
        <el-form :model="newForm"
                 :rules="rules"
                 ref="ruleForm"
                 label-width="100px"
                 style="width:70%">
          <el-form-item label="招募标题："
                        prop="title">
            <el-input v-model="newForm.title"
                      maxlength="10"
                      placeholder="招募标题不能超过10个字；招募标题不能为空"
                      show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="宣传图："
                        prop="recruitUrl">
            <el-upload :action="action"
                       list-type="picture-card"
                       :limit="1"
                       name="files"
                       :class="{hide:hideUpload}"
                       accept="image/png,image/jpg,image/jpeg"
                       :on-change="stopUpload"
                       :on-remove="handleRemove"
                       :before-upload="beforeAvatarUpload"
                       :on-success="handleAvatarSuccess">
              <div slot="tip"
                   class="el-upload__tip">为保证手机端页面美观，支持jpg、png、jpeg 格式，建议尺寸 750*1334尺寸，最多上传一张；</div>
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
        <!-- <el-button style="margin-left:30px"
                   type="text"
                   @click="showImgDia = true">查看示例</el-button>
        <el-form :model="newForm"
                 label-width="150px"
                 style="width:70%">
          <el-form-item label="分享朋友圈标题：">
            <el-input v-model="newForm.wxMomentsTitle"
                      type="textarea"
                      maxlength="20"
                      show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="分享好友标题：">
            <el-input v-model="newForm.wxFriendsTitle"
                      type="textarea"
                      maxlength="20"
                      show-word-limit></el-input>
          </el-form-item>
          <el-form-item label="分享好友文案：">
            <el-input v-model="newForm.wxFriendsContent"
                      type="textarea"
                      maxlength="30"
                      show-word-limit></el-input>
          </el-form-item>
        </el-form> -->
      </div>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="showNew = false">取 消</el-button>
        <el-button :loading="addLoading"
                   type="primary"
                   @click="submitForm('ruleForm')">立即创建</el-button>
      </span>
    </el-dialog>

    <!-- 示例弹出框 -->
    <el-dialog title="分享示例"
               :visible.sync="showImgDia"
               center>
      <img src="../../../assets/images/distribution/distriShowIMg.png"
           alt="">
    </el-dialog>
  </div>
</template>
<script>
import { objectUrl } from '@/utils/commom.js'
import {
  findChannelRecruitInfoList,
  addChannelRecruitInfo,   //新建分销员招募模板
  deleteChannelRecruitInfoById,  //删除分销员模板
  updateChannelRecruitInfo,     //分销员模板设置默认
} from '@/api/distributionManagement/distributor'
export default {
  props: ["status", "shop", "user"],
  data() {
    return {
      list: [],
      showNew: false,                    // 新增弹出框
      hideUpload: false,
      showImgDia: false,            //示例弹出框
      loading: false,
      action: '',
      newForm: {
        shopId: this.shop.shopId,
        title: '',
        wxMomentsTitle: '',
        wxFriendsTitle: '',
        wxFriendsContent: '',
        recruitUrl: '',
      },
      rules: {
        title: [
          { required: true, message: '请输入招募标题', trigger: 'blur' },
        ],
        recruitUrl: [
          { required: true, message: '请上传招募宣传图', trigger: 'blur' },
        ],
      },
      addLoading: false
    }
  },
  created() {
    this.getList();
    this.action = `/sys/business/shopChannel/uploadRecruitImage?shopId=${this.shop.shopId}`
  },
  mounted() {
    const clipboard = new this.clipboard(".cobyOrderSn");
  },
  methods: {
    getList() {
      this.loading = true
      findChannelRecruitInfoList({
        shopId: this.shop.shopId,
        pageSize: 50,
        pageNum: 1,
      }).then(res => {
        console.log('获取招募列表', res)
        this.list = res.data.list
        this.loading = false
      })
    },

    //预览
    showDetails(row) {
      console.log(row, this.$router)
      this.$router.push({
        path: '/distributionManagement/distributor/preview',
        query: {
          id: row.id
        }
      })
    },
    // 新增模板 的提交
    submitForm(formName) {
      if (this.list.length > 50) {
        this.$notify.error({
          title: '错误',
          message: '分销员招募模板最多为50条'
        });
        return
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addLoading = true;
          addChannelRecruitInfo(this.newForm).then(res => {
            console.log('添加模板', res)
            if (res.code == 200) {
              this.getList();
              this.$message({
                message: '新增成功',
                type: 'success'
              });
              this.showNew = false;
              setTimeout(() => {
                this.addLoading = false;
              }, 1500)
            } else {
              this.$message.error(res.message);
              this.addLoading = false;
            }
          })
        }
      });
    },
    // 展示新增模板
    goToNew() {
      this.showNew = true;
    },
    // 设置默认
    setRecruit(e) {
      let data = {};
      data.shopId = this.shop.shopId;
      data.id = e.id;
      data.isDefault = 1;
      updateChannelRecruitInfo(data).then(res => {
        console.log('设置默认', res)
        if (res.data == true) {
          this.$message({
            message: '设置成功',
            type: 'success'
          });
          this.getList();
        } else {
          this.$message.error(res.message);
        }
      })
    },
    // 删除
    deleteItem(e) {
      let data = {};
      data.shopId = this.shop.shopId;
      data.id = e.id;
      this.$confirm('是否确定移除该推广链接？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        console.log(data, '----')
        deleteChannelRecruitInfoById(data).then(res => {
          if (res.data == true) {
            this.getList();
            this.$message({
              message: '删除成功',
              type: 'success'
            });
          } else {
            this.$message.error(res.message);
          }
        })
      }).catch(() => { });
    },
    // 下载宣传图
    getPic(e) {
      let data = {
        shopId: this.shop.shopId,
        type: 1,
        id: e.id,
        fileName: e.recruitUrl
      }
      let key = objectUrl(data)
      window.open(`/sys/business/shopChannel/downloadRecruitImage?` + key)
    },
    // 下载二维码
    getQrcode(e) {
      let data = {
        shopId: this.shop.shopId,
        type: 2,
        id: e.id,
        fileName: e.herfUrl
      }
      let key = objectUrl(data)
      window.open(`/sys/business/shopChannel/downloadRecruitImage?` + key)
    },
    // 更换上传的图片
    stopUpload(file, fileList) {
      this.hideUpload = fileList.length >= 1
    },
    // 删除图片
    handleRemove(file, fileList) {
      // fileList.splice(1)
      this.hideUpload = fileList.length >= 1
      this.newForm.recruitUrl = ''
    },
    // 图片上传前
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isLt2M;
    },
    // 图片上传后
    handleAvatarSuccess(res, file, fileList) {
      this.hideUpload = fileList.length >= 1
      console.log(this.hideUpload)
      this.newForm.recruitUrl = res.data[0].name;
    },
    // 复制链接
    copyLink() {
      this.$message({
        message: '复制成功',
        type: 'success'
      });
    }
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  watch: {
    // startTime(newValue, oldValue) {
    //   this.getList()
    //   console.log(newValue)
    //   return newValue;
    // },
  }
}
</script>
<style lang="scss" scoped>
.hide /deep/.el-upload--picture-card {
  display: none;
}
.disBet {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.disStr {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.app-container {
  padding: 20px 20px 40px 20px;
}
.header {
  width: 100%;
}
.headNav {
  width: 100%;
}
.btn {
  width: 100px;
  text-align: center;
  height: 40px;
  line-height: 40px;
  color: #fff;
  background-color: #909399;
  cursor: pointer;
}
.active {
  background-color: #409eff;
}
.border {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}
.mr10 {
  margin-right: 10px;
}
.posFather {
  position: relative;
}
.posBox {
  position: absolute;
  right: 40px;
  top: 20px;
}
.disBet {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.disStar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.page {
  background-color: #fff;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: 32px;
}
</style>